

.grid-item {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.grid-container {
    max-width: 60%;
    margin-left: 20%;
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 30px;   
    border-bottom: 5px;
    
}
.grid-item label {
    margin-bottom: 5px; 
  
}

table {
    margin-top: 50px;
    min-width: 90%;
    margin-left: 5%;

}

.table {
    border-collapse: collapse;
  }
  
  .table th,
  .table td {
    border: none;
    padding: 8px;
  }
  
    
th {
    color: rgb(80, 80, 80);
    text-align:initial;
}
td {
    text-align: left;
}

thead tbody table th td {
    border: solid 1px;
}

.id{
    text-align: center;
}

tr:nth-child(odd) {background-color: #f2f2f2;}
tr:nth-child(even) {background-color: #fdfdfd;}
tr:hover {background-color: #c5d7e0;}